@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
#editor {
	width: 100%;
	height: 100%;
	position: fixed;
	background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAD0AAAA8BAMAAAAnEK9/AAAAAXNSR0IArs4c6QAAAARnQU1BAACxjwv8YQUAAAAGUExURVFRUTMzM3nhs84AAAAJcEhZcwAADsMAAA7DAcdvqGQAAAAhSURBVEjHY6AYCOIBAqPyo/Kj8qPyo/Kj8sNUnhLAwAAAui1roY+vV8MAAAAASUVORK5CYII=);
	background-repeat: repeat;
	background-size: 25px;
}

#editor-menu {
	width: 100%;
	display: inline-block;
	background-color: #242424;
	padding-bottom: 10px;
	padding-top: 5px;
}

#editor-menu>h1 {
	color: white;
	margin: 0;
	padding: 0;
	font-size: 21px;
	float: right;
	margin-right: 10px;
}

#editor-menu>div {
	display: inline-block;
	margin-left: 10px;
}

#editor-menu>div>button {
	background-color: #494949;
	color: white;
	border: none;
	padding: 5px;
	margin-right: 5px;
	cursor: pointer;
}
html, body, div {
	margin: 0;
	padding: 0;
	font-family: 'Roboto', sans-serif;
}
